@import "../../assets/styles/animations.scss";
@font-face {
  font-family: neon;
  src: url("../../assets/fonts/NEON.TTF");
}
.Photography {
  // margin-top: 100px;
  height: auto;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.container{
  text-align: center;
  margin: 20px;
  padding: 0;
  display: grid;
  grid-template-columns:auto auto auto auto;
  // grid-auto-rows: 50px  100px;
  grid-gap: 2vmin;
  grid-auto-flow: dense;
}

.galleryItem{
  width: 100%;
  height: 100%;
  position: relative;

  .image{
    width: 100%;
    height: 100%;
    overflow: hidden;
    // div{
    //   width: 200px;
    //   height:  200px;
    //   background-color: red;
    // }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
      cursor: pointer;
      transition: 0.5s ease-in-out;
      filter: grayscale(1) brightness(0.75);

    }

    img:hover {
      transform: scale(1.5);
      filter: grayscale(0);
    }


  }

  .text{
    // width: 100%;
    // height: 100%;
    position: absolute;
    top : 50%;
    left: 50%;
    opacity: 0;
    transform: translate(-50%,-50%);
    // background-color: rgba(0,0,0,0.3)
    pointer-events: none;
    z-index: 4;
    color: white;
    transition: 0.3s ease-in-out;
    backdrop-filter:blur(5px) saturate(1.8) brightness(0.85);
    -webkit-backdrop-blur:blur(5px) saturate(1.8) brightness(0.85);
    width: 100%;
    font-size: 1rem;
  }

}

.galleryItem:hover .text{
  opacity: 1;
  animation: move-down 0.3s linear;
}


.container .galleryContainer:first-child{
  grid-column-start:   span 2;
  grid-row-start:  span 2;
}

.container .galleryContainer:nth-child(2n+3){
  grid-row-start:  span 2;
  // grid-column-start:   span 2;
}

.container .galleryContainer:nth-child(4n+5){
  grid-row-start:  span 2;
  grid-column-start:   span 2;
}
.container .galleryContainer:nth-child(6n+7){
  grid-row-start:  span 1;
  // grid-column-start:   span 2;
}

.container .galleryContainer:nth-child(8n+9){
  grid-row-start:  span 1;
  grid-column-start:   span 1;
}

.pageDescription{
  color: rgb(255,102,153);
  font-size: 1.5vw;
}

.pageTitle{
  font-family: neon;
  color: rgb(0,188,212);
  padding: 50px 50px 0 50px;
  font-size: 5vw;
  position: relative;
  top: 0%;
  display: flex;
  // filter: blur(5px);
  span{
    position: absolute;
    width: 100%;
    height: 100%;
    filter: blur(8px);
  }

}

